.spin-logo {
}

@media (prefers-reduced-motion: no-preference) {
  .spin-logo {
    animation: spin-logo-spin infinite 10s linear;
  }
}

@keyframes spin-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.spin-logo-letters {
	margin: 0 !important;
  	color: white;
}
